@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Chakra Petch";
  font-display: swap;
  src: url("../fonts/ChakraPetch-Light.woff2") format("woff2"), url("../fonts/ChakraPetch-Light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Chakra Petch";
  font-display: swap;
  src: url("../fonts/ChakraPetch-Regular.woff2") format("woff2"), url("../fonts/ChakraPetch-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Chakra Petch";
  font-display: swap;
  src: url("../fonts/ChakraPetch-SemiBold.woff2") format("woff2"), url("../fonts/ChakraPetch-SemiBold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Chakra Petch", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  background-color: #b0b0b2;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
  z-index: 2;
}
.wrapper--dark {
  position: relative;
  background-color: #18181b;
}
.wrapper--dark::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  background-image: url("../img/decor/decor-top.png");
  background-repeat: no-repeat;
  background-position: 100% 0;
  background-size: contain;
  max-width: 736px;
  width: 100%;
  height: 354px;
}
.wrapper--dark::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  background-image: url("../img/decor/decor-bottom.png");
  background-repeat: no-repeat;
  background-position: 0 100%;
  background-size: contain;
  max-width: 1010px;
  width: 100%;
  height: 498px;
}

.rouble {
  /* stylelint-disable */
  font-weight: 400 !important;
  /* stylelint-enable */
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 120px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  position: fixed;
  top: 0;
  z-index: 100;
  overflow: hidden;
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 114px;
  backdrop-filter: blur(5px);
}
@media (max-width: 767px) {
  .header {
    display: block;
    z-index: 100;
  }
}
.header__wrapper {
  padding: 31px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 40px;
}
.header__top-block {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .header__top-block {
    width: 100%;
  }
}
@media (max-width: 767px) {
  .header__nav {
    position: absolute;
    top: 80px;
    bottom: 0;
    width: calc(100% - 32px);
    height: fit-content;
    padding: 50px 0;
    background-color: #18181b;
    visibility: hidden;
    opacity: 0;
    overflow-y: auto;
    transition: opacity 0.3s ease, visibility 0.3s ease;
  }
}
.header__nav-list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  gap: 12px 64px;
  list-style: none;
}
@media (max-width: 1023px) {
  .header__nav-list {
    gap: 12px 36px;
  }
}
@media (max-width: 767px) {
  .header__nav-list {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
}
@media (max-width: 767px) {
  .header__nav-item {
    width: 100%;
    padding: 10px;
  }
  .header__nav-item .link {
    font-size: 24px;
  }
}
.header .link.is-active {
  opacity: 1;
  text-shadow: 0 0 1.5px #ffffff, 0 0 1.5px #ffffff;
}
.header .btn {
  width: 126px;
}
@media (max-width: 767px) {
  .header .btn {
    display: none;
  }
}
.header__toggle {
  position: relative;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
  border: none;
  background-color: rgba(255, 255, 255, 0);
  cursor: pointer;
  display: none;
}
.header__toggle svg {
  width: 14px;
  height: 10px;
  transition: transform 0.3s ease;
}
@media (max-width: 767px) {
  .header__toggle {
    display: block;
  }
}
@media (max-width: 767px) {
  .header.is-open {
    height: 100vh;
  }
  .header.is-open .header__nav {
    display: flex;
    flex-direction: column;
    align-items: start;
    visibility: visible;
    opacity: 1;
  }
  .header.is-open .header__toggle svg {
    transform: rotate(180deg);
  }
}

.footer {
  position: relative;
  z-index: 10;
  padding: 40px 0;
}
.footer__top {
  padding-bottom: 35px;
  display: grid;
  grid-template-columns: 30% 64.5%;
  column-gap: 62px;
}
@media (max-width: 1279px) {
  .footer__top {
    grid-template-columns: 1fr;
    gap: 56px 0;
  }
}
@media (max-width: 419px) {
  .footer__top {
    gap: 30px 0;
  }
}
.footer__col1 {
  padding-bottom: 33px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (max-width: 1279px) {
  .footer__col1 {
    padding-bottom: 0;
    flex-direction: row;
  }
}
@media (max-width: 419px) {
  .footer__col1 {
    flex-direction: column;
    row-gap: 30px;
  }
}
.footer__col2 {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  column-gap: 30px;
}
@media (max-width: 767px) {
  .footer__col2 {
    justify-content: flex-start;
    gap: 56px;
  }
}
@media (max-width: 419px) {
  .footer__col2 {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .footer .contacts {
    width: 100%;
  }
}
.footer__nav:first-child {
  margin-right: 30px;
}
.footer__nav {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  list-style: none;
}
.footer__copyright {
  position: relative;
  padding-top: 32px;
  font-weight: 300;
  font-size: 16px;
  line-height: 28px;
  display: block;
  text-align: center;
  color: #ffffff;
}
.footer__copyright::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 0.5px;
  background: linear-gradient(90deg, #0cbaf1 0%, #e95ce9 100%);
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .modal__close-btn:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
.modal__close-btn.focus-visible:focus {
  opacity: 0.5;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.btn {
  padding: 18px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 14px;
  line-height: 14px;
  color: #ffffff;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  white-space: nowrap;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}
.btn--gradient {
  background: linear-gradient(90deg, #0cbaf1 0%, #e95ce9 100%);
}
.btn--border-gradient {
  border: 1px solid rgba(255, 255, 255, 0);
  border-image: linear-gradient(90deg, #0cbaf1 0%, #e95ce9 100%);
  border-image-slice: 1;
}
.btn--border-gradient-fw300 {
  padding: 12px;
  font-weight: 300;
  font-size: 16px;
  line-height: 28px;
  text-transform: initial;
  letter-spacing: normal;
}
.btn--icon {
  width: 48px;
  height: 48px;
  padding: 16px;
  background-color: rgba(255, 255, 255, 0.21);
}
.btn--icon svg {
  width: 16px;
  height: 16px;
}

.link {
  padding: 0;
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0);
  border: none;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.link.focus-visible:focus {
  opacity: 0.8;
}
.link--header {
  font-size: 16px;
  line-height: 18px;
  font-weight: 300;
  transition: text-shadow 0.3s ease;
}
@media (hover: hover) {
  .link--header:hover:not(.focus-visible) {
    text-shadow: 0 0 1.5px #ffffff, 0 0 1.5px #ffffff;
    opacity: 1;
  }
}
.link--header.focus-visible:focus {
  text-shadow: 0 0 1.5px #ffffff, 0 0 1.5px #ffffff;
  opacity: 1;
}

.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
  width: 100%;
  padding: 15px 60px 15px 48px;
}
@media screen and (max-width: 767px) {
  .custom-input,
.custom-textarea {
    width: 100%;
  }
}
.custom-input label,
.custom-textarea label {
  display: flex;
  align-items: center;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .custom-input label,
.custom-textarea label {
    align-items: flex-start;
    flex-direction: column;
  }
}
.custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
  flex-grow: 1;
  min-height: 24px;
  border: 2px solid rgba(255, 255, 255, 0);
  border-radius: 2px;
  outline: none;
  background-color: rgba(255, 255, 255, 0);
}
@media screen and (max-width: 767px) {
  .custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
    max-width: unset;
    width: 100%;
  }
}
.custom-input input::placeholder,
.custom-textarea input::placeholder {
  color: #ffffff;
}
@media (max-width: 419px) {
  .custom-input input::placeholder,
.custom-textarea input::placeholder {
    font-size: 12px;
  }
}
@media screen and (max-width: 767px) {
  .custom-input__label,
.custom-textarea__label {
    margin: 0 0 18px 0;
  }
}
.custom-input.is-invalid input,
.custom-input.is-invalid textarea,
.custom-textarea.is-invalid input,
.custom-textarea.is-invalid textarea {
  border: 2px solid #ff4747;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover,
.custom-input.is-invalid textarea:focus,
.custom-input.is-invalid textarea:hover,
.custom-textarea.is-invalid input:focus,
.custom-textarea.is-invalid input:hover,
.custom-textarea.is-invalid textarea:focus,
.custom-textarea.is-invalid textarea:hover {
  border: 2px solid rgba(255, 71, 71, 0.5);
